@import url('https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap');

:root{
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body{
  font-family: "Noto Serif";
  color: var(--secondary);
}
.App{
  max-width: 960px;
  margin: 0 auto;
}
.title h1{
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}
.title h2, .title p{
  text-align: center;
}
.title h2{
  margin-top: 60px;
  font-size: 2.6rem;
}

/* upload form styles */
.form{
  margin: 30px auto 10px;
  text-align: center;
}
.uploadForm label input{
  width: 0;
  height: 0;
  opacity: 0;

}
.uploadForm label{
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid var(--primary);
  border-radius: 50%;
  margin: 10px auto;
  line-height: 30px;
  color: var(--primary);
  font-weight: bold;
  font-size: 24px;
}

.uploadForm label:hover{
background: var(--primary);
  color: white;
}
.uploadForm  label span{
  padding: 8px;
}

.output{
  height: 60px;
  font-size: 0.8rem;
  text-align: center;
}

.error{
  color: var(--error);
}

/* progress bar styles */
.progress-bar
{
  height: 5px;
  margin-top:20px ;
  background: var(--primary);
}

/* image grid styles */
.img-grid{
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  

}
.img-wrap{
  overflow: hidden; 
  height: 0;
  padding: 50% 0; /* padding decides the height , making it perfect square regrdless of image height  */
  position: relative;
  opacity: 0.8;
}

.img-wrap img{
  min-width: 100%;
  max-width: 150%;
  min-height: 100%;
  position: absolute;
  top: 0;
  left: 0;

}

/* modal styles */
.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}
.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 60%;
  margin: 60px auto;
  border: 3px solid #ffffff;

}